    background-position: center;
    transition: all 0.4s ease; }

.form-checkbox input[type="checkbox"]:not(:checked) ~ label:hover::before {
  border: 1px solid #9bd2f4; }

.form-checkbox input[type="checkbox"]:checked ~ label::before {
  background-color: #2c8ecc; }

.form-checkbox input[type="checkbox"]:checked ~ label::after {
  display: block; }

.form-checkbox input[type="checkbox"]:disabled ~ label {
  cursor: text; }
  .form-checkbox input[type="checkbox"]:disabled ~ label::before {
    background-color: #f9f9fa; }
  .form-checkbox input[type="checkbox"]:disabled ~ label::after {
    display: block;
    background-image: url("../img/checkbox-disabled.svg"); }
  .form-checkbox input[type="checkbox"]:disabled ~ label:hover::before {
    border: 1px solid rgba(161, 167, 174, 0.5); }

.form-radio {
